  justify-content: flex-start;
  padding: var(--padding-xl);
  gap: var(--gap-11xl);
  margin-left: auto;
  margin-right: auto;
  position: relative !important;
  left: auto !important;
}
.newsletter-form-section1 {
  width: 1440px;
  height: 529px;
  overflow: hidden;
  flex-shrink: 0;
  justify-content: flex-end;
  padding: var(--padding-23xl) var(--padding-0);
  background-image: url(./public/newsletter-form-section1@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.logo-icon2 {
  position: relative;
  width: 174px;
  height: 33px;
  overflow: hidden;
  flex-shrink: 0;
}
.fickle-flight-is2 {
  align-self: stretch;
  position: relative;
  line-height: 27px;
}
.social-icons2 {
  position: relative;
  width: 130px;
  height: 30px;
}
.fickleflight-bio2 {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-sm);
  margin-bottom: 40px !important;
}
.seperator1 {
  position: relative;
  border-right: 1px solid var(--color-gray-400);
  box-sizing: border-box;
  width: 1px;
  height: 157.87px;
}
.company5 {
  position: relative;
  line-height: 27px;
  font-weight: 500;
  display: inline-block;
  width: 174.72px;
}
.about-us2,
.careers2,
.how-we-work2,
.news2 {
  position: relative;
  display: inline-block;
  width: 135.67px;
}
.careers2,
.how-we-work2,
.news2 {
  width: 84.28px;
}
.careers2,
.how-we-work2 {
  width: 115.11px;
}
.how-we-work2 {
  width: 197.33px;
}
.company4,
.frame10 {
  width: 197.33px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame10 {
  height: 112px;
  overflow: hidden;
  flex-shrink: 0;
  gap: var(--gap-xs);
  font-size: var(--typography-body-1-size);
}
.company4 {
  height: 159.76px;
  gap: var(--gap-xl);
}
.support5 {
  position: relative;
  line-height: 27px;
  font-weight: 500;
  display: inline-block;
  width: 131.56px;
}
.accessibility2,
.account2,
.faq2,
.more5 {
  position: relative;
  display: inline-block;
  width: 109.63px;
}
.accessibility2,
.faq2,
.more5 {
  width: 52.99px;
}
.accessibility2,
.more5 {
  width: 164.44px;
}
.more5 {
  line-height: 27px;
  font-weight: 500;
  width: 76.65px;
}
.airline-fees2,
.covid-advisory2,
.tips2 {
  position: relative;
  display: inline-block;
  width: 172.87px;
}
.airline-fees2,
.tips2 {
  width: 135.36px;
}
.tips2 {
  width: 50.56px;
}
.footer-links2,
.footer2,
.more4 {
  display: flex;
  align-items: flex-start;
}
.more4 {
  width: 197.33px;
  height: 159.76px;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-xl);
}
.footer-links2,
.footer2 {
  flex-direction: row;
}
.footer-links2 {
  flex: 1;
  justify-content: space-between;
  font-size: var(--font-size-xl);
}
.footer2 {
  background: linear-gradient(91.74deg, #0b5ba8 25.38%, #299bd8);
  justify-content: flex-start;
  padding: var(--padding-14xl) var(--padding-65xl);
  gap: var(--gap-25xl);
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--background-paper);
  font-family: var(--typography-body-1);
}
.footer-section3,
.footer2 {
  align-self: stretch;
}
.footer-section2,
.footer-section3,
.results-page {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.footer-section2 {
  align-self: stretch;
  text-align: center;
  font-size: var(--font-size-lg);
  color: var(--color-cornflowerblue-200);
}
.results-page {
  position: relative;
  background-color: var(--background-paper);
  width: 100%;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-42xl);
  color: var(--background-paper);
  font-family: var(--typography-body-1);
}
@media screen and (max-width: 1200px) {
  .top-header2 {
    padding-left: var(--padding-21xl);
    padding-right: var(--padding-21xl);
    box-sizing: border-box;
  }
  .search-filters {
    display: none;
  }
  .footer2,
  .search-results {
    padding: var(--padding-21xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 960px) {
  .navigation-menu2 {
    display: none;
  }
  .hamburger-menu-icon2 {
    display: flex;
  }
  .top-header2 {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
  }
  .where-are-you {
    font-size: var(--font-size-23xl);
    line-height: 48px;
  }
  .inputs-row1 {
    width: 100%;
    flex: unset;
    align-self: stretch;
  }
  .search-flights-button1 {
    margin-right: auto;
  }
  .button-group1 {
    width: 100%;
    text-align: left;
  }
  .search-form1 {
    flex-direction: column;
  }
  .search-section1 {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
    box-sizing: border-box;
    width: 100%;
    top: calc(44% - 96.5px - 0px);
  }
  .airline-section {
    flex-direction: column;
  }
  .flight-cards {
    flex: unset;
    align-self: stretch;
  }
  .results-section1 {
    flex-direction: column;
    gap: 80;
  }
  .search-results,
  .subscribe-form1 {
    padding: var(--padding-xl) var(--padding-5xl);
    box-sizing: border-box;
  }
  .subscribe-form1 {
    padding: var(--padding-5xl) var(--padding-0);
  }
  .fickleflight-bio2 {
    flex: unset;
    align-self: stretch;
  }
  .seperator1 {
    display: none;
  }
  .footer-links2 {
    flex: unset;
    align-self: stretch;
  }
  .footer2 {
    flex-direction: column;
    padding: var(--padding-21xl) var(--padding-5xl) var(--padding-11xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .navigation-right2 {
    display: flex;
  }
  .where-are-you {
    font-size: 31px;
    line-height: 40px;
  }
  .input-group {
    width: 100%;
    flex: unset;
    align-self: stretch;
  }
  .inputs-row1 {
    flex-direction: column;
  }
  .search-flights-button1 {
    width: 100% !important;
  }
  .search-section1 {
    width: 100%;
    top: calc(32% - 96.5px - 0px);
  }
  .turkish-icon {
    transform: scale(0.8);
  }
  .airline-section {
    flex-direction: row;
    width: 100% !important;
  }
  .flightduration {
    transform: scale(0.9);
  }
  .flight-details-section {
    flex: unset;
    align-self: stretch;
  }
  .price-section-child {
    display: none;
  }
  .flight-card {
    flex-direction: column;
    padding: var(--padding-xl);
    box-sizing: border-box;
  }
  .flightduration1 {
    transform: scale(0.9);
  }
  .button10 {
    width: 100%;
  }
  .button10:hover {
    background-color: var(--color-orange-200);
  }
  .subscribe-form1 {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    position: relative !important;
    left: auto !important;
    width: 100%;
  }
  .company4 {
    margin-bottom: 40px !important;
  }
  .footer-links2 {
    flex-direction: column;
  }
  .footer2 {
    padding: var(--padding-11xl) var(--padding-5xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 350px) {
  .am,
  .sin1 {
    font-size: var(--typography-body-1-size);
  }
  .departure-details {
    width: 80px;
  }
  .h-55m-non-stop {
    font-size: var(--font-size-smi);
  }
  .am1 {
    font-size: var(--typography-body-1-size);
  }
  .arrival-details {
    width: 80px;
  }
}
